/* search page here */
.searchbar{
  width: 50%;
  height: auto;
  margin: 0 auto;
  text-align: center;
}
.searchbar input{
 margin: 30px 0;
 padding: 5px 10px;
 border-radius: 5px;
 font-family: 'Josefin Sans', sans-serif;
 outline: none;
}
.myimg img{
  width: 30rem;
  height: 23rem;

}
@media (max-width:800px){
  .searchbar input{
    margin: 40px 0;
    padding: 5px 5px;
   }
  .myimg img{
    width: 15rem;
    height: 10rem;
    
  
  }
}
